<template>
    <Transition name="modal">
        <div class="modal" @click="closeModal">
            <div class="modal__block" @click.stop="">
                <h2 class="modal__block_title">{{ edit ? 'Изменить заметку' : 'Добавить заметку' }}</h2>

                <div class="modal__block_inputs">
                    <label>
                        <span>Title</span>
                        <input v-model="title" type="text" placeholder="Название заметки ...">
                    </label>
                    <label>
                        <span>Content</span>
                        <textarea v-model="text" placeholder="Текст заметки ...."></textarea>
                    </label>
                </div>

                <div class="modal__block_btns">
                    <button class="modal__block_btns-btn del" @click="closeModal">Отмена</button>

                    <button v-show="!edit" class="modal__block_btns-btn edit" @click="addNote">Добавить</button>

                    <button v-show="edit" class="modal__block_btns-btn edit" @click="changeNote">Изменить</button>

                </div>

            </div>
        </div>
    </Transition>
</template>

<script>

import { v4 as uuidv4 } from 'uuid';
export default {
    props: {
        edit: {
            typeof: Boolean
        },
        editedNotes: {
            typeof: Object
        }
    },
    data() {
        return {
            title: '',
            text: ''
        }
    },
    methods: {
        closeModal() {
            this.$emit('close')
            this.title = this.text = ''
        },
        addNote() {

            if (this.title != '' && this.text != '') {
                const newNote = {
                    id: uuidv4(),
                    title: this.title,
                    /* 26.04.2023 */
                    date: new Date().toLocaleDateString(),
                    text: this.text
                }
                this.$emit('addNote', newNote)
                this.closeModal()
            }

        },
        changeNote() {
            if (this.title.length > 2 && this.text.length > 2) {
                const newEditedNote = {
                    id: this.editedNotes.id,
                    title: this.title,
                    date: new Date().toLocaleDateString(),
                    text: this.text
                }
                this.$emit('changedNote', newEditedNote)
                this.closeModal()
            }

        }
    },
}
</script>

