<template>
  <Navbar @setSearch="searchValue = $event" />

  <Notes :notes="filterNotes" @delNote="delNote" @change="change" />

  <Modal v-show="isModalOpen" @close="isModalOpen = false" @addNote="addNote" :edit="edit" :editedNotes="editedNotes"
    @changedNote="changedNote" />

  <button v-show="!isModalOpen" class="addNote" @click="isModalOpen = true, edit = false">
    <img src="@/assets/img/pens.svg" alt="">
  </button>
</template>

<script>
import Modal from './components/Modal.vue';
import Navbar from './components/Navbar.vue';
import Notes from './components/Notes.vue';



export default {
  components: {
    Navbar,
    Notes,
    Modal
  },
  data() {
    return {
      notes: [],
      isModalOpen: false,
      edit: false,
      editedNotes: null,
      searchValue: ''
    }
  },
  methods: {
    addNote(newNote) {
      this.notes.push(newNote)
    },
    getNotes() {
      let localNotes = localStorage.notes
      if (localNotes) {
        this.notes = JSON.parse(localNotes)
      }
    },

    delNote(id) {
      this.notes.splice(id, 1)
    },
    change(id) {
      this.isModalOpen = this.edit = true
      let currentNote = this.notes.find(note => note.id == id)
      this.editedNotes = currentNote
    },
    changedNote(newNote) {
      this.notes.forEach(note => {
        if (note.id == newNote.id) {
          note.title = newNote.title
          note.text = newNote.text
          note.date = newNote.date
        }

      })
      console.log(newNote);
    }
  },
  watch: {
    // text(newValue){
    //   console.log('Новое значение ' + newValue);
    // }

    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes)
        console.log('Изменён');
      },
      deep: true
    }
  },
  mounted() {
    this.getNotes()
  },
  computed: {
    filterNotes() {
      return this.searchValue ?
        this.notes.filter(note => note.title.toLowerCase().includes(this.searchValue.toLowerCase())) : this.notes
    }
  }
}
</script>

